* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

.auth_error {
  list-style: none;
  font-weight: 700;
  margin-left: -10px;
}

.wallet_icon {
  width: 32px;
}

.alert_action {
  text-decoration: none;
  text-transform: uppercase;
  padding: 2px 3px;
  background-color: #890F0D;
  color: antiquewhite;
  border: 1px solid white;
  border-radius: 5px;
}

.link {
  text-decoration: none;
  color: white;
}

.text-black {
  color: black;
}